.swiper-button-prev:after,
.swiper-button-next:after {
  display: none;
}

.main-slider {
  &-section-header {
    position: relative;
    padding-right: 130px;

    @include m-b-down(534px) {
      padding-right: 20px;
    }

    .lw-section-title {
      margin-bottom: 0;
    }

    margin-bottom: 40px;
  }

  margin-bottom: 20px;

  .swiper-wrapper {
    width: fit-content;
    padding: 30px 0 40px;
    margin: 0px auto;
  }

  .swiper-slide {
    width: 240px;
    height: 340px;
    box-shadow: var(--main-slider-item-shadow);

  }

  &-button-prev,
  &-button-next {
    right: 0;

    width: 44px;
    height: 44px;
    border-radius: 50%;

    box-shadow: var(--main-slider-button-shadow);
    background-color: var(--main-slider-button-color);

    svg {
      fill: var(--accent);
    }

    @include m-b-down(534px) {
      display: none;
    }
  }

  &-button-prev {
    left: initial;
    right: 60px;

    svg {
      transform: rotate(180deg);
    }
  }

  &__item {
    position: relative;
    width: inherit;
    height: 100%;

    picture {
      display: flex;
      width: 100%;
      height: 100%;

      img {
        object-fit: contain;
      }
    }
  }

  &--examples {
    .main-slider__item {
      border: 2px solid var(--accent);
    }

    .swiper-slide {
      box-shadow: none;
    }
    .swiper-wrapper {
      padding: 0;
      margin: 0;
    }

    .caption {
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      width: 100%;
      height: fit-content;
      min-height: 60px;
      font-size: 14px;
      padding: 5px;
      color: var(--white);
      background-color: rgba(0,0,0,.8);
    }
  }
}
