.zoom-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,.8);

  z-index: 201;
}

.zoom-popup{
  button {
    @extend .button-default;
    @extend .flex-centered-content;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 11;

    svg {
      fill: var(--white);
    }

    background-color: rgba(0,0,0,.2);
    padding: 10px;
    border-radius: 2px;
  }

  img {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translate(-50%,-50%);
    height: fit-content;
    max-height: 95vh;
    width: fit-content;
    max-width: 95vw;
    object-fit: contain;
  }
}

