*,
*::before,
*::after {
  box-sizing: border-box;
  border-radius: 0;

  @extend .main-scroll;
}

html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 16px;
  color: var(--main-color);
  background-color: var(--main-bg-color);

  overflow-x: hidden !important;

  &.body-locked {
    overflow: hidden;
  }
}

footer {
  margin-top: auto;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,h2,h3,h4,h5,h6,p {
  margin: 0;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type=number] {
	-moz-appearance: textfield;
}

input,
fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.anchor-link {
  margin-bottom: 40px;
}

.hero__content > .anchor-link {
  margin-bottom: 0;
}

section {
  width: 100%;
  @extend .lw-section;
}
